* {
  box-sizing: border-box;
  outline: none; }

html, body, div, span, applet, object,
h1, h2, h3, h4, h5, h6, p,
a {
  margin: 0;
  padding: 0;
  border: 0; }

a {
  text-decoration: none;
  color: inherit; }

