.custom-grid-three-elements {
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(4, minmax(150px, 1fr));
  grid-template-rows: repeat(4, 132.75px);
  grid-row-gap: 6px;
  grid-column-gap: 6px; }
  .custom-grid-three-elements .item1 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    -ms-grid-column: 1;
    -ms-grid-column-span: 2;
    -ms-grid-row: 1;
    -ms-grid-row-span: 4;
    grid-column: 1/3;
    grid-row: 1/5; }
  .custom-grid-three-elements .item2 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    -ms-grid-column: 3;
    -ms-grid-column-span: 2;
    -ms-grid-row: 1;
    -ms-grid-row-span: 2;
    grid-column: 3/5;
    grid-row: 1/3; }
  .custom-grid-three-elements .item3 {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    -ms-grid-column: 3;
    -ms-grid-column-span: 2;
    -ms-grid-row: 3;
    -ms-grid-row-span: 2;
    grid-column: 3/5;
    grid-row: 3/5; }

