.wrapper-section-title {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px; }
  .wrapper-section-title__content {
    font-size: 32px;
    font-weight: 900;
    color: #000000; }

.link {
  background-color: #00db92;
  border-radius: 20px;
  display: inline-flex;
  transition: transform .3s ease-out; }
  .link:hover {
    transform: translate(0, -5px);
    -webkit-box-shadow: -8px 10px 5px -6px #71a896;
    -moz-box-shadow: -8px 10px 5px -6px #71a896;
    box-shadow: -8px 10px 5px -6px #71a896; }
  .link__title {
    font-size: 18px;
    font-weight: 900;
    padding: 10px 7px 10px 25px;
    letter-spacing: -0.14px; }
  .link__icon {
    width: 15px;
    height: 15px;
    align-self: center;
    padding-right: 35px; }
    .link__icon .arrow {
      border: solid #FFFFFF;
      border-width: 0 2.5px 2.5px 0;
      display: inline-block;
      padding: 2.5px; }
    .link__icon .right {
      transform: rotate(-45deg);
      -webkit-transform: rotate(-45deg); }

.wrapper-custome-section {
  width: 85.375%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  margin-top: 90px; }

.text-grid-title {
  margin-top: 8px;
  padding: 0 0px 28px 28px;
  transition: all .40s ease-out; }
  .text-grid-title__xl {
    font-size: 36px;
    font-weight: 900; }
  .text-grid-title__l {
    font-size: 24px;
    font-weight: 900;
    margin-right: 100px; }
  .text-grid-title__m {
    font-size: 18px;
    font-weight: 900;
    margin-right: 5px; }
  .text-grid-title:hover {
    color: #00db92; }

@media all and (min-width: 1600px) {
  .text-grid-title__l {
    margin-right: 182px; }
  .text-grid-title__m {
    margin-right: 30px; } }

.wrapper-text-grid {
  display: flex;
  flex-direction: row;
  margin-left: 28px; }
  .wrapper-text-grid svg {
    width: 25px;
    height: 25px;
    margin-right: 8px;
    transition: transform .8s ease-out;
    overflow: visible; }
    .wrapper-text-grid svg #Rectangle {
      stroke-width: 6; }
  .wrapper-text-grid svg:hover {
    transform: scale(1.2);
    transition: transform .2s ease-in; }
    .wrapper-text-grid svg:hover #Triangle {
      fill: white; }
    .wrapper-text-grid svg:hover #Rectangle {
      stroke: #00db92; }
  .wrapper-text-grid .hidde {
    display: none; }
  .wrapper-text-grid__category {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
    align-self: center; }
